<svelte:options customElement="fds-presets-dialog"></svelte:options>
<script>
    import {get_current_component} from "svelte/internal"
    import {onMount, tick} from "svelte";
    import swal from "sweetalert";

    export let modifierdata = {};
    export let presetsdialogopen = false;
    export let reopendialogevent = false;
    export let modifiers = "";
    export let dialogdata;
    export let presetsselectedtabindex = 0;
    export let onlyexternalmodifiers = false;
    let host = get_current_component().$$.root.host
    let importjsondialogopen;
    let exportjsondialogopen;
    let exmodifiersloaded;
    let dialogrefopen;
    let internalclose = false;
    let name = "";
    let prompt = "";
    let override_prompt
    let newtagel
    let newtagellistadded
    let presets = [];
    let filteredpresets = [];
    let taglist = [];
    let extaglist = [];
    let tagelement;
    let selectedtag;
    let eltagsexternal = 'All'
    let exfilteredpresets = []
    let comptags = []
    let overviewel, editel, examplepromptel
    let nameel, modifiersel
    let newtag = "";
    let type        // SDcloud, DALL-E 2...
    let selected_tab_index=0;
    let tabindexes = ["externalModifiers","localModifiers"];

    // fake ones
    let Config = {};

    function action(name, value,actiontype) {
        host.dispatchEvent(new CustomEvent("action", {detail: {name: name, value: value,actiontype}}))
    }

    onMount(()=> {
        load();
        dialogrefopen = true;
        internalclose = false;
        setTimeout(() => {
           // let selectedval = tabindexes[presetsselectedtabindex];
           // selectTab(selectedval);
        }, "100");

    })


    $:{
        if(reopendialogevent){
            setTimeout(() => {
                if(typeof presetsselectedtabindex === "undefined") presetsselectedtabindex = 0;
                let selectedval = tabindexes[presetsselectedtabindex];
                selected_tab_index = presetsselectedtabindex;
                selectedTab = selectedval;
                selectTab(selectedval);
            }, "300");
            presets = [...presets];
            internalclose = false;
            reopendialogevent = false;
        }
    }
    $:{
        if(dialogdata){
            console.log("dialogdata",dialogdata);
            modifiers = dialogdata.modifiers.replace(/<br\/>/g, '\r');
            prompt = dialogdata.prompt;
        }
    }

    $:{
        if (presets && presets.length && selectedtag && selectedtag != 'All') {
            filteredpresets = presets.filter((el) => {
                let tmptagarr = el.tags.split(",");
                return tmptagarr.includes(selectedtag);
            })
        } else if (presets && presets.length) {
            filteredpresets = [...presets];
        }
        if (filteredpresets) {
            filteredpresets = filteredpresets.map((el) => {
                let modifiers = el.modifiers.replace(/\r/g, '<br/>');
                return {...el, modifiers};
            });
        }
    }


    $:{
        if (externalModifiers && eltagsexternal && eltagsexternal != 'All') {
            let typeFilter = "sd"
            if (Config && Config.generalConfig && Config.generalConfig.type === "DALL-E 2") typeFilter = "dalle2"
            exfilteredpresets = externalModifiers.filter((el) => {
                let tmptagarr = el.tags;
                return (tmptagarr.includes(eltagsexternal) && tmptagarr.includes(typeFilter));
            })
        } else if (externalModifiers) {
            exfilteredpresets = [...externalModifiers];
        }
        if (exfilteredpresets) {
            exfilteredpresets = exfilteredpresets.map((el) => {
                let modifiers = el.modifiers.replace(/\r/g, '<br/>');
                modifiers = modifiers.replace(/\n/g, '<br/>');
                return {...el, modifiers};
            });
        }
    }


    $:{
        if (false && presetsdialogopen) {
                load();
                dialogrefopen = true;
                presetsdialogopen = false;
        }
    }

    $: {
        if (newtagel && !newtagellistadded) {
            newtagellistadded = true;
            window.addEventListener('keydown', (ev) => {
                if (ev.keyCode === 13 && ev.target && ev.target.id == 'newtag' && ev.target.value) {
                    addExistingTag(ev.target.value);
                    setTimeout(function () {
                        ev.target.value = "";
                    }, 200);
                    ev.preventDefault();
                    return false;
                }
            });
        }
    }

    const dispatchEvent =  (event,detail) => {
        host.dispatchEvent(new CustomEvent(event, {detail: detail}))
    }

    const load = async () => {
        //type = Config.generalConfig.type
        presets = modifierdata.list || [];
        if (!presets) return;
        filteredpresets = presets.map((el) => {
            let modifiers = el.modifiers.replace(/\r/g, '<br/>');
            return {...el, modifiers};
        });

        let alltagsfromrecods = [];
        presets.forEach((el) => {
            let tmptag = el.tags.split(',');
            alltagsfromrecods = [...alltagsfromrecods, ...tmptag];
        })
        let tlist = [...alltagsfromrecods];
        taglist = tlist.filter(function (el) {
            return el != null && el != "" && el != " ";
        });
        taglist = taglist.filter((v, i, a) => a.indexOf(v) === i);

    }

    const removeTagFromPreset = async (tagel) => {
        console.log("removeTagFromPreset", tagel);
        comptags = comptags.filter((el) => {
            return el != tagel
        });
        console.log("removeTagFromPreset111", comptags);
    }


    const addAndSelect = async () => {
        let tagsa = comptags || [];
        let tags = tagsa.toString();
        window.tags = null;
        presets = presets.filter((el) => {
            return el.name != name
        });
        //dispatchEvent("change",{name, prompt, override_prompt, modifiers, tags});
        presets.push({name, prompt, override_prompt, modifiers, tags});
        presets = presets;


        let alltagsfromrecods = [];
        presets.forEach((el) => {
            let tmptag = el.tags.split(',');
            alltagsfromrecods = [...alltagsfromrecods, ...tmptag];
        })
        taglist = [...alltagsfromrecods];
        taglist = taglist.filter((v, i, a) => a.indexOf(v) === i);
        taglist = taglist.filter(function (el) {
            return el != null && el != "" && el != " ";
        });

        modifierdata.tags = taglist.toString();
        modifierdata.list = presets;
        let strignified = JSON.stringify(modifierdata);
        dispatchEvent("change",{modifierdata:strignified});
        overviewMode();
        //dialogrefopen.close();
    }

    const editSelectedRec = async (rec) => {
        await editMode();
        await tick();
        modifiers = rec.modifiers.replace(/<br\/>/g, '\r');
        name = rec.name;
        prompt = rec.prompt;
        override_prompt = rec.override_prompt
        let tcomptags = rec.tags.split(",");
        comptags = tcomptags.filter(function (el) {
            return el != null && el != "" && el != " ";
        });
    }

    const deleteSelectedRec = async (rec) => {
        const ok = await swal({
            text: "Are you sure to delete the preset?",
            buttons: ["Cancel", "OK"],
        });
        if (!ok) return
        presets = presets.filter((el) => {
            return el.name != rec.name
        });
        modifierdata.list = presets;
        await Config.save();
    }

    const selectRec = async (rec) => {

        dispatchEvent('select',rec);
        //dialogrefopen=false;
        internalclose = true;
    }

    const closeDialog =   () => {
        internalclose = true;
    }

    const editMode = async () => {
        overviewel.style.display = 'none';
        editel.style.display = 'block';
    }
    const overviewMode = async () => {
        overviewel.style.display = 'block';
        editel.style.display = 'none';
    }

    const addExistingTag = async (ntag) => {
        let tcomptags = [...comptags, ntag];
        comptags = tcomptags.filter(function (el) {
            return el != null && el != "" && el != " ";
        });
    }


    let selectedTab = "externalModifiers"
    let externalModifiers = []


    const selectTab = async (name) => {
        selected_tab_index=  tabindexes.indexOf(name);
        selectedTab = name;
        if (selectTab == "localModifiers") return;
        if (exmodifiersloaded) return;
        let currentDateTime = new Date()
        let time = currentDateTime.getTime() / 1000
        let url = "https://www.flyingdog.de/modifiers/db/presetsdata.json?" + time
     //   url = "http://localhost:3333/presetsdata.json?" + time
        try {
            let res = await fetch(url, {
                headers: {
                    "Content-Type": "application/json",
                    "Accept": "application/json"
                },
                method: 'get'
            })
            externalModifiers = await res.json()
            externalModifiers = externalModifiers;
            externalModifiers.forEach((el) => {
                extaglist = [...extaglist, ...el.tags]
            });
            extaglist = [...new Set(extaglist)];
            console.log(externalModifiers)
            exmodifiersloaded = true;
        } catch (error) {
            await swal("Server error", error.toString() + "\n" + url)
        }
    }

    const importjson = async () => {
        importjsondialogopen = true;
        setTimeout(function () {
            importjsondialogopen = false;
        }, 200);
    }

    const exportjson = async () => {
        exportjsondialogopen = true;
        setTimeout(function () {
            exportjsondialogopen = false;
        }, 200);
    }

    const modifiersjsonsetterfunc = async (newjsonval) => {
        if (!newjsonval.trim()) return;
        let addjson = JSON.parse(newjsonval);
        if (addjson) presets = [...presets, ...addjson];
    }

    const deleteStyles   = async () => {
        dispatchEvent('select', {});
        internalclose = true;
    }


</script>
{#if dialogrefopen && !internalclose}
    <div class="generate">
    <svg on:click={closeDialog} xmlns="http://www.w3.org/2000/svg" height="18" viewBox="0 0 18 18" width="18" class="closeBtn" alt="Close">
        <rect id="Canvas" fill="#ff13dc" opacity="0" width="18" height="18"></rect>
        <path class="fill"
              d="M13.2425,3.343,9,7.586,4.7575,3.343a.5.5,0,0,0-.707,0L3.343,4.05a.5.5,0,0,0,0,.707L7.586,9,3.343,13.2425a.5.5,0,0,0,0,.707l.707.7075a.5.5,0,0,0,.707,0L9,10.414l4.2425,4.243a.5.5,0,0,0,.707,0l.7075-.707a.5.5,0,0,0,0-.707L10.414,9l4.243-4.2425a.5.5,0,0,0,0-.707L13.95,3.343a.5.5,0,0,0-.70711-.00039Z"></path>
    </svg>
    <div class="title">Presets</div>

    <!---
    <input type="text" placeholder="Describe what to generate" class="formInput">
    <textarea class="formInput modifierInput" placeholder="Modfiers, one per line"></textarea>
    --->
    <button on:click={deleteStyles} variant="primary" class="nostyle">No Style</button>
    <div style="position: relative" id="overview" bind:this={overviewel}>

        <fds-tabs selected_tab_index="{selected_tab_index}">
            <div class={onlyexternalmodifiers ? "onlyexternalmodifiers" : ""} slot="tabs">

                <fds-tab on:click={(ev) => {selectTab("externalModifiers"); action("changeGeneralConfig",0,'presetsSelectedTabIndex')}}
                         style="color: rgba(255, 255, 255, 0.6); position: relative;"><span class="tabinfo">Modifier Ideas</span>
                </fds-tab>

                <fds-tab on:click={(ev) => {selectTab("localModifiers");action("changeGeneralConfig",1,'presetsSelectedTabIndex') }}
                         style="color: rgba(255, 255, 255, 0.6); position: relative;"><span class="tabinfo">Local Modifiers</span>
                </fds-tab>

            </div>
            <div slot="content">
                <fds-tab-content>
                    <div style="margin-top: 20px; margin-bottom: 10px;">
                        <label>Select a tag filter</label>
                        <select class="formInputfilter" id="anotherone" style="width:150px; margin-bottom: 8px; "
                                placeholder="Select a tag filter..."
                                on:change={(ev) => (eltagsexternal = ev.target.value)}>
                            <option>All</option>
                            {#each extaglist as mtagel}
                                {#if mtagel !== "sd" && mtagel !== "dalle2"}
                                    <option>{mtagel}</option>
                                {/if}
                            {/each}
                        </select>
                    </div>


                    <div class={selectedTab==="externalModifiers" ? "sp-tab-page visible externalModifiers" : "sp-tab-page"}>
                        <div class="repeater">
                            {#each exfilteredpresets as modifier, i}
                                {#if (type === "DALL-E 2" && modifier.tags.includes("dalle2")) || (type !== "DALL-E 2" && modifier.tags.includes("sd"))  }
                                    <div on:click={selectRec(modifier)} class="repeater-item">
                                        <div class="title">{modifier.key}</div>
                                        {#if type === "DALL-E 2"}
                                            <img src={modifier.d2_imageurl} class="image" alt={modifier.key}>
                                        {:else}
                                            <img src={modifier.imageurl} class="image" alt={modifier.key}>
                                        {/if}
                                        <div class="modifiers">
                                            <p class="modifierstext">{@html modifier.modifiers}</p>
                                            <p class="modifiersexample_prompt">{modifier.example_prompt}</p>
                                        </div>

                                    </div>
                                {/if}
                            {/each}
                        </div>
                    </div>
                </fds-tab-content>
                <fds-tab-content>
                    <div class={selectedTab==="localModifiers" ? "sp-tab-page visible" : "sp-tab-page"}>
                        {#if taglist.length}
                            <div style="margin-top: 20px; margin-bottom: 10px;">
                                <label>Select a tag filter</label>
                                <select class="formInputfilter" style="width: 150px; margin-bottom: 8px;"
                                        placeholder="Select a tag filter..."
                                        on:change={(ev) => (selectedtag = ev.target.value)}>
                                    <option>All</option>
                                    {#each taglist as tagel}
                                        <option>{tagel}</option>
                                    {/each}
                                </select>
                            </div>
                        {/if}
                        <button on:click={editMode} variant="primary" class="addbutton">
                            <div slot="icon" style="fill: currentColor">
                                <svg xmlns="http://www.w3.org/2000/svg" height="18" viewBox="0 0 18 18" width="18">
                                    <rect id="Canvas" fill="#ff13dc" opacity="0" width="18" height="18"/>
                                    <path class="fill"
                                          d="M14.5,8H10V3.5A.5.5,0,0,0,9.5,3h-1a.5.5,0,0,0-.5.5V8H3.5a.5.5,0,0,0-.5.5v1a.5.5,0,0,0,.5.5H8v4.5a.5.5,0,0,0,.5.5h1a.5.5,0,0,0,.5-.5V10h4.5a.5.5,0,0,0,.5-.5v-1A.5.5,0,0,0,14.5,8Z"/>
                                </svg>

                            </div>
                            Add
                        </button>


                        <div style="position:relative;">


                            <div class="container">
                                <div class="card__wrap--outer">
                                    {#each filteredpresets as preset,idx}

                                        <div style="position: relative" class="card__wrap--inner">
                                            <div class="card">

                                                <div class="card__item" on:click={selectRec(preset)}>
                                                    <h2>{preset.name}</h2>
                                                </div>

                                                <div class="card__item flexible" on:click={selectRec(preset)}>
                                                    <small>{@html preset.modifiers}</small>
                                                </div>

                                                <div class="delbutton" on:click={deleteSelectedRec(preset)} slot="icon"
                                                     style="fill: currentColor">
                                                    <svg xmlns="http://www.w3.org/2000/svg" height="18"
                                                         viewBox="0 0 18 18"
                                                         width="18">
                                                        <rect id="Canvas" fill="#ff13dc" opacity="0" width="18"
                                                              height="18"/>
                                                        <path class="fill"
                                                              d="M13.7455,4l-1.154,12H4.4085L3.2545,4ZM11,1H6A1,1,0,0,0,5,2V3H1.25A.25.25,0,0,0,1,3.25v.5A.25.25,0,0,0,1.25,4h1L3.4565,16.55a.5.5,0,0,0,.5.45H13.046a.5.5,0,0,0,.5-.45L14.75,4h1A.25.25,0,0,0,16,3.75v-.5A.25.25,0,0,0,15.75,3H12V2A1,1,0,0,0,11,1ZM6,3V2h5V3Z"/>
                                                        <path class="fill"
                                                              d="M8.5,14.5A.5.5,0,0,1,8,14V6A.5.5,0,0,1,9,6v8A.5.5,0,0,1,8.5,14.5Z"/>
                                                        <path class="fill"
                                                              d="M10.467,14.5a.5.5,0,0,1-.46718-.5308L10,13.9665l.5355-7.9975a.50112.50112,0,1,1,1,.067L11,14.034a.5.5,0,0,1-.53176.46608Z"/>
                                                        <path class="fill"
                                                              d="M6.533,14.5a.5.5,0,0,0,.46718-.5308L7,13.9665,6.4625,5.969a.50112.50112,0,1,0-1,.067l.5355,8a.5.5,0,0,0,.53348.46411Z"/>
                                                    </svg>
                                                </div>


                                                <div class="edbutton" on:click={editSelectedRec(preset)} slot="icon"
                                                     style="fill: currentColor">
                                                    <svg xmlns="http://www.w3.org/2000/svg" height="18"
                                                         viewBox="0 0 18 18"
                                                         width="18">
                                                        <rect id="Canvas" fill="#ff13dc" opacity="0" width="18"
                                                              height="18"/>
                                                        <path class="fill"
                                                              d="M16.7835,4.1,13.9,1.216a.60751.60751,0,0,0-.433-.1765H13.45a.6855.6855,0,0,0-.4635.203L2.542,11.686a.49494.49494,0,0,0-.1255.211L1.0275,16.55c-.057.1885.2295.4255.3915.4255a.12544.12544,0,0,0,.031-.0035c.138-.0315,3.933-1.172,4.6555-1.389a.486.486,0,0,0,.207-.1245L16.7565,5.014a.686.686,0,0,0,.2-.4415A.61049.61049,0,0,0,16.7835,4.1ZM5.7,14.658c-1.0805.3245-2.431.7325-3.3645,1.011L3.34,12.304Z"/>
                                                    </svg>
                                                </div>
                                            </div>
                                        </div>
                                    {/each}
                                </div>

                            </div>
                        </div>
                    </div>


                </fds-tab-content>
           </div>
        </fds-tabs>


        <!--- <button class="formInput ok" type="Start AI image generation">Save</button> --->

    </div>
    <div id="edit" bind:this={editel}>
        <div class="sp-body">

            <label>Name</label>
            <input bind:this={nameel} class="formInput" value="{name}" on:change={(ev) =>name = ev.target.value}/>

            <label>Modifiers</label>
            <textarea bind:this={modifiersel} placeholder="Modifiers" class="formInput modifierInput"
                      value="{modifiers}"
                      on:change={(ev) =>modifiers = ev.target.value}></textarea>

            <label>Tags</label>
            <div class="svelte-tags-input-layout" style="margin-top: 35px">
                {#each comptags as tag, i}
                                        <span class="svelte-tags-input-tag">
                                                {tag}
                                            <span class="svelte-tags-input-tag-remove"
                                                  on:pointerdown={() => removeTagFromPreset(tag)}> &#215;</span>

                                        </span>
                {/each}
            </div>

            <div class="flex-parent" style="margin-top: 10px">

                <div class="flex-item">

                    <input bind:this={newtagel} value={newtag}
                           on:change={(ev) => (addExistingTag(ev.target.value))} id="newtag" class="formInput"/>


                </div>
                <div class="flex-item">
                    <select class="formInput" style="width:100%; margin-bottom: 8px;"
                            placeholder="Select existing tag..."
                            on:change={(ev) => (addExistingTag(ev.target.value))}>
                        {#each taglist as tagel}
                            <options>{tagel}</options>
                        {/each}
                    </select>
                </div>

            </div>

            <label>Example Prompt</label>
            <input bind:this={examplepromptel} id="prompt" class="formInput" value="{prompt}"
                   on:change={(ev) =>prompt = ev.target.value}/>

            <label>Override Prompt</label>
            <input type="checkbox" title="Use Example Prompt to override the prompt text"
                   checked={override_prompt ? "checked" : undefined} class="checkbox"
                   on:change={(ev) => {override_prompt = false; if (ev.target.checked) override_prompt = true;}}/>
        </div>
        <footer>
            <div class="footerdiv">
                <button on:click={addAndSelect} variant="cta" class="formInput ok">Save</button>
                <button on:click={overviewMode} variant="primary" class="formInput cancel">Cancel</button>
            </div>
        </footer>
    </div>
</div>
{/if}

